<template>
    <div class="home animated">
        <div id="particles-js">
        </div>
        <div class="container animated fadeIn">
            <h1 class="name">Hu Bingliang</h1>
            <p>Replace the stars and rivers in the body with unlimited methods for the future.</p>
            <div class="nav">
                <svg class="icon" aria-hidden="true" @click="jump('https://github.com/hubingliang')">
                    <use xlink:href="#icon-github1"></use>
                </svg>
                <svg class="icon" aria-hidden="true" @click="jump('http://www.jianshu.com/u/7cff123bc7d8')">
                    <use xlink:href="#icon-blog"></use>
                </svg>
            </div>
        </div>
    </div>
</template>

<script>
export default {
  data(){
    return{}
  },
  mounted(){
    this.particles()
  },
  methods:{
      particles:function(){
        particlesJS("particles-js", {
          "particles": {
              "number": {
                  "value": 80,
                  "density": {
                      "enable": true,
                      "value_area": 800
                  }
              },
              "color": {
                  "value": "#9B957C"
              },
              "shape": {
                  "type": "circle",
                  "stroke": {
                      "width": 0,
                      "color": "#000000"
                  },
                  "polygon": {
                      "nb_sides": 5
                  },
                  "image": {
                      "src": "img/github.svg",
                      "width": 100,
                      "height": 100
                  }
              },
              "opacity": {
                  "value": 1,
                  "random": false,
                  "anim": {
                      "enable": false,
                      "speed": 1,
                      "opacity_min": 0.1,
                      "sync": false
                  }
              },
              "size": {
                  "value": 3,
                  "random": true,
                  "anim": {
                      "enable": false,
                      "speed": 50,
                      "size_min": 0.1,
                      "sync": false
                  }
              },
              "line_linked": {
                  "enable": true,
                  "distance": 300,
                  "color": "#ffffff",
                  "opacity": 0.1,
                  "width": 1
              },
              "move": {
                  "enable": true,
                  "speed": 8,
                  "direction": "none",
                  "random": false,
                  "straight": false,
                  "out_mode": "out",
                  "bounce": false,
                  "attract": {
                      "enable": false,
                      "rotateX": 600,
                      "rotateY": 1200
                  }
              }
          },
          "interactivity": {
              "detect_on": "canvas",
              "events": {
                  "onhover": {
                      "enable": false,
                      "mode": "repulse"
                  },
                  "onclick": {
                      "enable": false,
                      "mode": "push"
                  },
                  "resize": true
              },
              "modes": {
                  "grab": {
                      "distance": 800,
                      "line_linked": {
                          "opacity": 1
                      }
                  },
                  "bubble": {
                      "distance": 800,
                      "size": 80,
                      "duration": 2,
                      "opacity": 0.8,
                      "speed": 3
                  },
                  "repulse": {
                      "distance": 400,
                      "duration": 0.4
                  },
                  "push": {
                      "particles_nb": 4
                  },
                  "remove": {
                      "particles_nb": 2
                  }
              }
          },
          "retina_detect": true
        });
      },
      jump:function(url){
          window.open(url)
      }
  }
}
</script>



